<template>
  <div class="faultreporting">
    <div class="scroll_content_box">
      <list-item v-for="item in faultReportingData" :key="item.id" :to="`/faultreporting/detail/${item.id}`">
        <div slot="firstL">问题描述:</div>
        <div slot="firstR" class="tag" :class="getColor(item.deal_results)">{{item.deal_results}}</div>
        <div slot="second">{{item.break_describe || '暂无描述'}}</div>
        <div slot="thirdL">故障类型:</div>
        <div slot="thirdR">{{item.break_type}}</div>
        <div slot="fourthL">提报时间:</div>
        <div slot="fourthR">{{item.submit_time}}</div>
      </list-item>
      <no-item v-if="faultReportingData.length===0"></no-item>
      <div class="blank" v-else></div>
    </div>
    <bottom-btn to="/faultreporting/add">新增检查</bottom-btn>
  </div>
</template>

<script>
export default {
  name: 'FaultReporting',
  data () {
    return {
      faultReportingData: [
      ]
    }
  },
  methods: {
    getColor (v) {
      if (v === '已处理') {
        return 'yichuli'
      } else {
        return 'weichuli'
      }
    },
    getBreakData () {
      this.$rpc.call('funenc_xa_station.break_submit', 'get_break_submit_list', [], {}).then(res => {
        this.faultReportingData = res
      }).catch(e => {
        console.log(e)
        alert('获取故障列表失败')
      })
    }
  },
  mounted () {
    this.getBreakData()
  }
}
</script>

<style lang="less" scoped>
@import '../../styles/common';
.faultreporting{
  min-height: 100vh;
  .scroll_content_box{
    overflow-y: scroll;
    -webkit-overflow-scrolling : touch;
    height: 100%;
    box-sizing: border-box;
    .weichuli{
      color: @jujue;
      border: 1px solid @jujue;
    }
    .yichuli{
      color: @tongguo;
      border: 1px solid @tongguo;
    }
    .blank{
      height: 66 * @px1 !important;
    }
  }
}
</style>
